body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
body,
h1,
h2 {
  font-family: "fira sans";
}

.hero-header {
  margin-top: 150px;
}

.my-work-container {
  position: relative;
  width: 50%;
}

.my-work-img {
  display: block;
  width: 100%;
  height: auto;
}

.work-places-container {
  background-color: #8c43ff;
  padding-top: 50px;
  color: white;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border-radius: 50px;
  margin: auto;
  opacity: 0;
  transition: 0.5s ease;
  background-color: rgb(255, 255, 255);
}

.my-work-container:hover .overlay {
  opacity: 1;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.hero-skills {
  background: linear-gradient(
    to bottom,
    #8c43ff 0%,
    #8c43ff 50%,
    white 50%,
    white 100%
  );
  width: 100%;
  padding: 0;
}
.main-body {
  height: 1250px;
}
.organge {
  background-color: #ff7654;
}

.purple-text {
  color: #8c43ff;
}

.purple {
  background-color: "#6849e3";
}

nav h2 {
  color: white;
}

.cta-btn {
  border: 1px solid white !important;
  color: white !important;
  font-weight: 700 !important;
}

.hero-btn {
  border: 1px solid #8c43ff !important;
  color: #8c43ff !important;
  font-size: 1em !important;
  margin-top: 50px;
  font-weight: 700 !important;
}

.my-work-btn {
  border: 1px solid #8c43ff !important;
  color: #8c43ff !important;
  font-size: 1em !important;
  margin-top: 10px;
  font-weight: 700 !important;
}

.my-contact-btn {
  border: 1px solid white !important;
  color: white !important;
  float: none !important;
  margin-top: 10px;
  font-size: 1em !important;
  padding-left: 100px !important;
  padding-right: 100px !important;
  margin: 20px;
  font-weight: 700 !important;
}

.footer-text {
  color: #dadadaa9;
  font-size: 2em;
}

.my-contact-btn:hover {
  color: #8c43ff !important;
  background-color: white;
}

.my-work-btn:hover {
  background-color: #8c43ff;
  color: white !important;
}

.hero-btn:hover {
  background-color: #8c43ff;
  color: white !important;
}

.hero-intro {
  background-color: #8c43ff;
  color: white;
  padding-top: 150px;
  padding-bottom: 50px;
  padding-left: 50px;
}
.navbar-toggler {
  color: white !important;
}
.hero-skills {
  background-color: #8c43ff;
}

.skill-col-left {
  background-color: white;
  padding-top: 50px;
  border-radius: 5px 5px 5px 5px;
  height: 700px;
  margin-bottom: 50px !important;
  border: 0.05em solid #8c43ff;
  margin-right: 10px;
}

.purple-icon {
  color: #8c43ff;
  font-size: 3em;
  margin-bottom: 40px;
}

.skill-title {
  color: #8c43ff !important;
}
.skill-col-right {
  background-color: white;
  padding-top: 50px;
  border-radius: 5px 5px 5px 5px;
  height: 700px;
  margin-bottom: 50px !important;
  border: 0.05em solid #8c43ff;
  margin-left: 10px;
}

.hero-intro p {
  font-family: fira sans;
  font-weight: 200;
  font-size: 20px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

/* Add some top padding to the page content to prevent sudden quick movement (as the navigation bar gets a new position at the top of the page (position:fixed and top:0) */
.sticky + .content {
  padding-top: 60px;
}
.cta-btn:hover {
  background-color: white;
  color: #8c43ff !important;
}

.nav-link {
  float: right !important;
  color: white !important;
  font-weight: bold;
}

.nav-link:hover {
   color: rgb(161, 161, 161) !important;
   border-bottom: 3px solid white;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@media only screen and (max-width: 923px) {
  .main-body {
    height: 1300px;
  }
}

@media only screen and (max-width: 874px) {
  .main-body {
    height: 1400px;
  }
}

@media only screen and (max-width: 768px) {
  .main-body {
    height: 1400px;
  }

  .hero-btn {
    float: none !important;
  }
}

